.header {
	margin-bottom: 50px;
	padding: 10px 0 0 0;
}

.header__wrap {
	position: relative;
	padding: 0 6% 10px 6%;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
}

.header__social {
	width: 33.4%;
}

.header__logo {
	width: 33.3%;
	text-align: center;
	padding-left: 12px;
}

.header__img-logo {
	display: block;
	margin: 0 auto;
	width: 55px;
	height: 35px;
}

.header__btn-wrap {
	width: 33.3%;
	text-align: right;
}

.header__bg-wrap {
	position: relative;

	&::before {
		content: "";
		position: absolute;
		height: 7px;
		background-image: url("../img/wave.svg");
		background-repeat: repeat-x;
		background-size: 35px 7px;
		top: -1px;
		left: 0;
		right: 0;
		z-index: 2;
	}
}

.header__bg-image {
	width: 100%;
	height: auto;
}

.header__nav--no-js .nav__menu-opener {
	display: none;
}

.header__nav--no-js .nav__list {
	display: block;
}

.header__nav--menu-open .nav__list {
	display: block;
	position: absolute;
	top: 35px;
	right: 0;
	z-index: 10;
}

.header__nav--menu-open .nav__button-icon {
	width: 0;
	transition: 0.3s;
	right: 1px;

	&::before,
	&::after {
		width: 42px;
		transform: rotate(-45deg);
		transform-origin: 87% 50%;
		transition: 0.3s;
	}

	&::after {
		transform: rotate(45deg);
	}
}

@media (min-width: $mobile) {
	.header__social {
		flex-direction: row;
		align-content: center;
		height: auto;
	}

	.header__nav--no-js .nav__list {
		display: inline-flex;
		justify-content: space-between;
	}

	.header__nav--menu-open .nav__list {
		display: inline-flex;
		justify-content: center;
		width: 100%;
	}
}

@media (min-width: $mobileWide) {
	.header__logo {
		padding-left: 15px;
	}

	.header__img-logo {
		width: 65px;
		height: 45px;
	}

	.header__nav--menu-open .nav__list {
		top: 45px;
	}
}

@media (min-width: $tablet) {
	.header {
		margin-bottom: 80px;
	}

	.header__img-logo {
		width: 75px;
		height: 55px;
	}

	.header__logo {
		padding-left: 17px;
	}

	.header__bg-wrap::before {
		height: 15px;
		background-size: 50px 12px;
	}

	.header__nav--menu-open .nav__list {
		top: 55px;
	}
}

@media (min-width: $tabletWide) {
	.header {
		padding-top: 15px;
		margin-bottom: 100px;
	}

	.header__wrap {
		padding-bottom: 15px;
	}

	.header__img-logo {
		width: 85px;
		height: 65px;
	}

	.header__logo {
		padding-left: 20px;
	}

	.header__nav--menu-open .nav__list {
		top: 65px;
		padding-bottom: 10px;
	}
}

@media (min-width: $medium) {
	.header__btn-wrap {
		display: none;
	}

	.header__img-logo {
		width: 95px;
		height: 75px;
	}

	.header__logo {
		position: static;
		transform: none;
		order: -1;
		padding-left: 0;
	}

	.header__social {
		order: 1;
	}

	.header__wrap {
		justify-content: space-between;
	}

	.header__bg-wrap::before {
		height: 30px;
		background-size: 70px 15px;
	}

	.header__social,
	.header__logo,
	.header__nav {
		width: auto;
	}

	.header__nav--menu-open .nav__list {
		position: static;
		padding-bottom: 0;
	}
}
